import React from "react";
import ReactDOM from "react-dom";
import {
  //main component
  SearchBar,
  Page,
  //for display data
  Panel,
  PanelHeader,
  PanelBody,
  PanelFooter,
  MediaBox,
  MediaBoxHeader,
  MediaBoxBody,
  MediaBoxTitle,
  MediaBoxDescription,
  Cell,
  CellBody,
  CellFooter
} from "react-weui";
import "weui";
// 数据设置
import SampleData from "./nameDB";
import "react-weui/build/packages/react-weui.css";


const appMsgIcon = (
  <img src="data:image/png;base64," />
);

const CellMore = () => (
  <Cell access link>
    <CellBody>More</CellBody>
    <CellFooter />
  </Cell>
);

export default class SearchBarDemo extends React.Component {
  state = {
    searchText: "",
    results: []
  };

  handleChange(text, e) {
    let keywords = [text];
    let results = SampleData.filter(
      /./.test.bind(new RegExp(keywords.join("|"), "i"))
    );

    if (results.length > 3) results = results.slice(0, 3);
    this.setState({
      results,
      searchText: text
    });
  }

  render() {
    return (
      // <Page
      //   className="searchbar"
      //   title="SearchBar"
      //   subTitle="搜索栏"
      //   transition={false}
      // >
        <SearchBar
          onChange={this.handleChange.bind(this)}
          defaultValue={this.state.searchText}
          placeholder="搜索关键词/位置/公司"
          lang={{
            cancel: "取消"
          }}
        />

        /*{ <Panel
          style={{
            display: this.state.searchText ? null : "none",
            marginTop: 0
          }}
        >
          <PanelHeader>搜索关键词/位置/公司</PanelHeader>
          <PanelBody>
            {this.state.results.length > 0 ? (
              this.state.results.map((item, i) => {
                return (
                  <MediaBox key={i} type="appmsg" href="javascript:void(0);">
                    <MediaBoxHeader>{appMsgIcon}</MediaBoxHeader>
                    <MediaBoxBody>
                      <MediaBoxTitle>{item}</MediaBoxTitle>
                      <MediaBoxDescription>
                        You may like this name.
                      </MediaBoxDescription>
                    </MediaBoxBody>
                  </MediaBox>
                );
              })
            ) : (
              <MediaBox>没有搜索记录！</MediaBox>
            )}
          </PanelBody>
          <PanelFooter href="javascript:void(0);">
            <CellMore />
          </PanelFooter>
        </Panel>
      </Page> }*/
    );
  }
}



