import React from "react";
import "./campustalk.css";
import campusTalkImg from "./images/campustalk-img.png";

const CampusTalkModule = props => (
  <section
    className="campustalk-module"
    onClick={e => props.campusEvent(props.data.Seminar)}
  >
    <p className="campustalk-title">
      <span className="title">宣讲会</span>
      <i
        className="campustalk-close"
        onClick={e => {
          e.stopPropagation();
          props.closeCampus();
        }}
      />
    </p>
    <img src={campusTalkImg} width="100%" height="165" />
    <section className="campustalk-info">
      <div className="info-name">
        <span className="time">{props.data.Seminar.OccurDate}</span>
        <p className="title ellipsis">{props.data.Seminar.SeminarName}</p>
        <p className="address">
          {props.data.Seminar.LocatedCity} {props.data.Seminar.College}
        </p>
      </div>
    </section>
  </section>
);

export default CampusTalkModule;



