import React from "react";
import {
  Page,
  Flex,
  FlexItem,
  Gallery,
  GalleryDelete,
  Button,
  Icon
} from "react-weui";
import JobSub from "./job-sub";
import JobCompany from "./job-company";
import "./job-module.css";
import "./job-sub.css";
import "./job-company.css";

class JobModule extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    const ImgList = props =>
      props.imgs.map((item, index) => (
        <FlexItem key={index}>
          <div className="imglist">
            <img
              className="home-jobImg-placeholder"
              src={`//upload.zheyibu.com/${item}`}
              alt="职位"
              onClick={e => {
                props.gallery(index, props.imgs);
                e.stopPropagation();
              }}
            />
          </div>
        </FlexItem>
      ));
    return (
      <section
        className="job-module"
        onClick={e => this.props.job(this.props.data.Position)}
      >
        <JobSub position={this.props.data.Position} />

        {this.props.data.Position.EtpPhoto.length > 0 && (
          <section className="home-company-img">
            <Flex>
              <ImgList
                imgs={this.props.data.Position.EtpPhoto}
                gallery={this.props.gallery}
              />
            </Flex>
          </section>
        )}

        <JobCompany
          etpLogo={this.props.data.Position.EtpLogoUrl}
          etpName={this.props.data.Position.EtpName}
          etpArea={this.props.data.Position.IndustryNames}
          etpStatus={this.props.data.Position.EtpCertStatus}
        />
      </section>
    );
  }
}

export default JobModule;



