import React from "react";
import axios from "axios";
import { Page, Button, Toast } from "react-weui";
import GlobalHeader from "../header/header";
import "./campustalk_details.css";

class CampusDetailsModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      campus_details: "",
      showLoading: false
    };
  }

  componentWillMount() {
    this.showLoading();
  }

  componentDidMount() {
    let url =
      "//api.zheyibu.com/api/CampusTalk/" + this.props.details.SeminarId;
    axios
      .get(url)
      .then(res => {
        const campusDetails = res.data;
        this.setState({
          campus_details: campusDetails,
          showLoading: false
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  showLoading() {
    this.setState({ showLoading: true });
  }
  render() {
    return (
      <section className="campus-details-module">
        <div className="just-overlay">
          <GlobalHeader headerName="宣讲会详情" goBack={this.props.goBack} />
          <section className="campus-details">
            <p className="campus-details-title">
              {this.state.campus_details.SeminarName}
            </p>
            <section className="campus-info">
              <div className="info-item">
                <div className="item-label">
                  <i className="time" />
                  <span className="item-name">时间：</span>
                </div>
                <div className="item-info">
                  {this.state.campus_details.Time}
                </div>
              </div>
              <div className="info-item">
                <div className="item-label">
                  <i className="time" />
                  <span className="item-name">公司：</span>
                </div>
                <div className="item-info">
                  {this.state.campus_details.CompanyName}
                </div>
              </div>
              <div className="info-item">
                <div className="item-label">
                  <i className="time" />
                  <span className="item-name">学校：</span>
                </div>
                <div className="item-info">
                  {this.state.campus_details.College}
                </div>
              </div>
              <div className="info-item">
                <div className="item-label">
                  <i className="time" />
                  <span className="item-name">地点：</span>
                </div>
                <div className="item-info">
                  {this.state.campus_details.Address}
                </div>
              </div>
            </section>
            <section className="campus-details-intro">
              <p className="campus-intro">宣讲会介绍</p>
              <section
                className="campus-details-content"
                dangerouslySetInnerHTML={{
                  __html: this.state.campus_details.Description
                }}
              />
            </section>
          </section>
          <Toast icon="loading" show={this.state.showLoading}>
            加载中...
          </Toast>
        </div>
      </section>
    );
  }
}

export default CampusDetailsModule;



