import React from "react";
import { Flex, FlexItem } from "react-weui";
import "./nav.css";
import home_campus from "./home-campus.png";
import home_article from "./home-article.png";
import home_fire from "./home-fire.png";
import home_resume from "./home-resume.png";

const Nav = props => (
  <section className="nav">
    <Flex>
      <FlexItem>
        <a href="" className="nav-list">
          <img
            className="nav-placeholder"
            src={home_campus}
            alt="宣讲会"
            width="47"
            height="47"
          />
          <p className="nav-list-title">宣讲会</p>
        </a>
      </FlexItem>
      <FlexItem>
        <a href="" className="nav-list">
          <img
            className="nav-placeholder"
            src={home_article}
            alt="求职攻略"
            width="47"
            height="47"
          />
          <p className="nav-list-title">求职攻略</p>
        </a>
      </FlexItem>
      <FlexItem>
        <a href="" className="nav-list">
          <img
            className="nav-placeholder"
            src={home_fire}
            alt="投递简历"
            width="47"
            height="47"
          />
          <p className="nav-list-title">投递简历</p>
        </a>
      </FlexItem>
      <FlexItem>
        <a href="" className="nav-list">
          <img
            className="nav-placeholder"
            src={home_resume}
            alt="我的简历"
            width="47"
            height="47"
          />
          <p className="nav-list-title">我的简历</p>
        </a>
      </FlexItem>
    </Flex>
  </section>
);

export default Nav;



