import React from "react";
import { Flex, FlexItem } from "react-weui";
import "./job-sub.css";

const JobSub = props => (
  <section className="position" key={props.position.PositionId}>
    <span className="position-salary ellipsis">{props.position.SalaryName}</span>
    <p className="position-title ellipsis">
      {props.position.Position}
    </p>
    <p className="position-sub">
      {props.position.CityName} |{" "}
      {props.position.PositionType == 1 ? "全职" : "实习"} |{" "}
      {props.position.RecruitCount + "人"}
      <span className="position-time">{props.position.DeployTime}</span>
    </p>
  </section>
);

export default JobSub;



