// javascript
import React from "react";
import ReactDOM from "react-dom";
// react-weui
import {
  Page,
  Tab,
  TabBody,
  TabBar,
  TabBarItem,
  TabBarIcon,
  TabBarLabel,
  Gallery,
  GalleryDelete,
  Button,
  Icon,
  Dialog
} from "react-weui";
// weui
import "weui";
import axios from "axios";
// 搜索虚拟数据
import SearchBarDemo from "./searchBar/searchBar";
// home
import Banner from "./home/banner";
import Nav from "./home/nav";
// job_module
import JobModule from "./job_module/job-module";
// job_details
import JobDetailsModule from "./job/jobModule";
// campustalk
import CampusTalkModule from "./campustalk/campustalk";
// campustalk_details
import CampusDetailsModule from "./campustalk/campustalk_details";
// article
import ArticleModule from "./article/article";
// article_details
import ArticleDetailsModule from "./article/article_details";
// stylesheet
import "react-weui/build/packages/react-weui.css";
import "./index.css";
// images
import Explore from "./images/explore.png";
import ExploreAc from "./images/exploreAc.png";
import Fire from "./images/fire.png";
import FireAc from "./images/fireAc.png";
import Message from "./images/message.png";
import MessageAc from "./images/messageAc.png";
import Self from "./images/self.png";
import SelfAc from "./images/selfAc.png";
// import App from "./App";
// import registerServiceWorker from "./registerServiceWorker";

class HomeIndex extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tab: 0,
      posts: [],
      // 职位详情
      showJob: false,
      _job: {},
      // 查看相册
      showGallery: false,
      _gallery: [],
      _index: 0,
      // 查看求职攻略
      showArticle: false,
      _article: {},
      // 查看宣讲会
      showCampus: false,
      _campus: {},
      // 提示框
      iosStyle: {
        buttons: [
          {
            type: "default",
            label: "Cancel",
            onClick: this.handleCloseCampus.bind(this)
          },
          {
            type: "primary",
            label: "OK",
            onClick: this.handleCloseCampus.bind(this)
          }
        ]
      },
      showIOS: false
    };

    // 处理职位详情查看事件
    this.handleJob = this.handleJob.bind(this);
    // 处理相册查看事件
    this.handleGallery = this.handleGallery.bind(this);
    // 处理求职攻略查看事件
    this.handleArticle = this.handleArticle.bind(this);
    // 处理宣讲会查看事件
    this.handleCampus = this.handleCampus.bind(this);
    // 删除推荐求职攻略
    this.handleCloseArticle = this.handleCloseArticle.bind(this);
    // 删除推荐宣讲会
    this.handleCloseCampus = this.handleCloseCampus.bind(this);
  }

  componentDidMount() {
    let url =
      "//zheyibu.com/app/recomm/recommends/v2?ClientId=1&PageSize=10&Page=1&Major=120102&GraduateDate=20170808&CityIds=1001&IsRefresh=0&UserId=47589";
    axios
      .get(url)
      .then(res => {
        const posts = res.data.Data.map(obj => obj);
        this.setState({ posts });
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  handleJob(job) {
    this.setState(prevState => ({
      showJob: !prevState.showJob,
      _job: job
    }));
  }

  handleGallery(index = 0, gallery = []) {
    this.setState(prevState => ({
      showGallery: !prevState.showGallery,
      _index: index,
      _gallery: gallery
    }));
  }

  handleArticle(details) {
    this.setState(prevState => ({
      showArticle: !prevState.showArticle,
      _article: details
    }));
  }
  handleCloseArticle() {
    this.setState(prevState => ({
      showIOS: !prevState.showIOS
    }));
  }

  handleCampus(details) {
    this.setState(prevState => ({
      showCampus: !prevState.showCampus,
      _campus: details
    }));
  }
  handleCloseCampus() {
    this.setState(prevState => ({
      showIOS: !prevState.showIOS
    }));
  }

  render() {
    return (
      <section className="tab-boss">
        {/* 发现主页 */}
        <Tab>
          <TabBody>
            <Page
              transition={false}
              style={{ display: this.state.tab == 0 ? null : "none" }}
            >
              <SearchBarDemo />
              <Banner />
              <Nav />
              <section className="line-break">— 为你推荐的好职位 —</section>
              <section className="recommend-list">
                <ModuleRender
                  posts={this.state.posts}
                  job={this.handleJob}
                  gallery={this.handleGallery}
                  articleEvent={this.handleArticle}
                  campusEvent={this.handleCampus}
                  closeArticle={this.handleCloseArticle}
                  closeCampus={this.handleCloseCampus}
                />
              </section>
            </Page>
          </TabBody>
          <TabBar>
            <TabBarItem
              active={this.state.tab == 0}
              onClick={e => this.setState({ tab: 0 })}
              icon={
                this.state.tab == 0 ? (
                  <img src={ExploreAc} />
                ) : (
                  <img src={Explore} />
                )
              }
              label="发现"
            />
            <TabBarItem
              active={this.state.tab == 1}
              onClick={e => this.setState({ tab: 1 })}
              icon={
                this.state.tab == 1 ? <img src={FireAc} /> : <img src={Fire} />
              }
              label="求职"
            />
            <TabBarItem
              active={this.state.tab == 2}
              onClick={e => this.setState({ tab: 2 })}
              icon={
                this.state.tab == 2 ? (
                  <img src={MessageAc} />
                ) : (
                  <img src={Message} />
                )
              }
              label="消息"
            />
            <TabBarItem
              active={this.state.tab == 3}
              onClick={e => this.setState({ tab: 3 })}
              icon={
                this.state.tab == 3 ? <img src={SelfAc} /> : <img src={Self} />
              }
              label="我的"
            />
          </TabBar>
          {/* 职位详情 */}
          {this.state.showJob && (
            <Page ptr={false} infiniteLoader={false} style={{ zIndex: "509" }}>
              <JobDetailsModule
                goBack={this.handleJob}
                details={this.state._job}
              />
            </Page>
          )}
          {/* 相册 */}
          {this.state.showGallery && (
            <Page ptr={false} infiniteLoader={false} style={{ zIndex: "509" }}>
              <Gallery
                src={this.state._gallery.map(value =>
                  encodeURI(`//upload.zheyibu.com/${value}`)
                )}
                show={this.state.showGallery}
                defaultIndex={this.state._index}
              >
                <Button onClick={e => this.handleGallery()} plain>
                  Back
                </Button>
              </Gallery>
            </Page>
          )}
          {/* 求职攻略详情 */}
          {this.state.showArticle && (
            <Page ptr={false} infiniteLoader={false} style={{ zIndex: "509" }}>
              <ArticleDetailsModule
                goBack={this.handleArticle}
                details={this.state._article}
              />
            </Page>
          )}
          {/* 宣讲会详情 */}
          {this.state.showCampus && (
            <Page ptr={false} infiniteLoader={false} style={{ zIndex: "509" }}>
              <CampusDetailsModule
                goBack={this.handleCampus}
                details={this.state._campus}
              />
            </Page>
          )}
          <Dialog
            type="ios"
            title={this.state.iosStyle.title}
            buttons={this.state.iosStyle.buttons}
            show={this.state.showIOS}
          >
            禁止删除
          </Dialog>
        </Tab>
      </section>
    );
  }
}
const ModuleRender = props =>
  props.posts.map((item, index) => {
    switch (item.Type) {
      case "Pos":
        return (
          <JobModule
            data={item}
            key={index}
            job={props.job}
            gallery={props.gallery}
          />
        );
      case "Art":
        return (
          <ArticleModule
            data={item}
            key={index}
            articleEvent={props.articleEvent}
            closeArticle={props.closeArticle}
          />
        );
      case "Sem":
        return (
          <CampusTalkModule
            data={item}
            key={index}
            campusEvent={props.campusEvent}
            closeCampus={props.closeCampus}
          />
        );
      default:
        return null;
    }
  });

// registerServiceWorker();
ReactDOM.render(<HomeIndex />, document.getElementById("root"));



