import React from "react";
import { Flex, FlexItem } from "react-weui";
import home_banner from "./home-banner.png";

const Banner = props => (
  <Flex>
    <FlexItem>
      <img
        className="banner-placeholder"
        src={home_banner}
        alt="公司信息"
        width="100%"
        height="100%"
      />
    </FlexItem>
  </Flex>
);

export default Banner;



