import React from "react";
import axios from "axios";
import { Page, Button, Toast, Dialog } from "react-weui";
import GlobalHeader from "../header/header";
import CompanyModule from "../company/companyModule";
import "./jobModule.css";

class JobDetailsModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      job_details: "",
      showLoading: false,
      // 评论
      ifFire: false,
      // 是否可以发送评论
      ifRemark: false,
      // 提示框
      iosStyle: {
        buttons: [
          {
            type: "default",
            label: "Cancel",
            onClick: this.handlePageClick.bind(this)
          },
          {
            type: "primary",
            label: "OK",
            onClick: this.handlePageClick.bind(this)
          }
        ]
      },
      showIOS: false,
      // 显示公司主页
      showCampus: false,
      companyId: null,
      startY: null
    };
    // 处理页面全局点击事件
    this.handlePageClick = this.handlePageClick.bind(this);
    // 点击切换评论栏目
    this.handleRemark = this.handleRemark.bind(this);
    // 处理输入框发布状态切换
    this.handleFireState = this.handleFireState.bind(this);
    // 切换公司主页
    this.handleCompanyHomepage = this.handleCompanyHomepage.bind(this);
    // 申请职位处理
    this.handleApplyJob = this.handleApplyJob.bind(this);
    // 发布评论
    this.handleFire = this.handleFire.bind(this);
  }

  componentWillMount() {
    this.showLoading();
  }

  componentDidMount() {
    let url =
      "//api.zheyibu.com/job/Details/v2?id=" + this.props.details.PositionId;
    axios
      .get(url)
      .then(res => {
        const job_details = res.data;
        this.setState({
          job_details: job_details,
          showLoading: false
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  }
  /**
   * [当前页面点击事件处理]
   */
  handlePageClick(e) {
    this.setState(prevState => ({
      showIOS: !prevState.showIOS
    }));
  }
  /**
   * [点击切换评论栏目]
   */
  handleRemark(e, msg) {
    e.stopPropagation(); //取消冒泡
    msg
      ? this.setState({
          ifFire: true
        })
      : this.setState({
          ifFire: false
        });
  }
  /**
   * [处理点评发布事件]
   */
  handleFire() {}
  /**
   * [处理输入框发布状态切换]
   */
  handleFireState() {
    let textInput = document.querySelector(".edit-remark").innerHTML;
    textInput
      ? this.setState({
          ifRemark: true
        })
      : this.setState({
          ifRemark: false
        });
  }

  handleCompanyHomepage(companyId) {
    this.setState(prevState => ({
      showCompany: !prevState.showCompany,
      companyId: companyId
    }));
  }

  handleApplyJob() {
    this.setState(prevState => ({
      showIOS: !prevState.showIOS
    }));
  }

  showLoading() {
    this.setState({ showLoading: true });
  }
  render() {
    return (
      <div className="layer-third-fix">
        <section
          className="job-details-module"
          onClick={e => this.handleRemark(e, false)}
        >
          <div className="just-overlay">
            <GlobalHeader headerName="职位详情" goBack={this.props.goBack} />
            <section className="job-details">
              <section className="job-details-info">
                <div className="job-info">
                  <div className="job-title">
                    <span className="job-salary">
                      【{this.state.job_details.SalaryRange}】
                    </span>
                    <span className="job-name">
                      {this.state.job_details.PositionName}
                    </span>
                    <p className="job-info-sub">
                      <span className="info-sub">
                        <i className="location" />
                        {this.state.job_details.CityName}
                      </span>
                      <span className="info-sub">
                        <i className="recruit" />
                        招聘{this.state.job_details.RecruitCount}人
                      </span>
                      <span className="info-sub">
                        <i className="jobType" />
                        {this.state.job_details.PositionTypeName}
                      </span>
                    </p>
                  </div>
                </div>
                <div
                  className="company-info"
                  onClick={e =>
                    this.handleCompanyHomepage(
                      this.state.job_details.EnterpriseId
                    )
                  }
                >
                  <img
                    src={this.state.job_details.EtpLogoUrl}
                    width="60"
                    height="60"
                  />
                  <div className="company-intro">
                    <p className="company-name ellipsis">
                      <span className="name">
                        {this.state.job_details.EnterpriseName}
                      </span>
                      {this.state.job_details.EtpCertificationStatus === 4 ? (
                        <i className="vip" />
                      ) : null}
                    </p>
                    <p className="company-area">
                      {this.state.job_details.IndustryIdNames}
                    </p>
                  </div>
                </div>
              </section>
              <section className="job-position">
                <p className="position-title">职位要求</p>
                <div className="job-position-details">
                  <p className="position-item">
                    <span className="item">学历要求：</span>
                    {this.state.job_details.DegreeNames}
                  </p>
                  <p className="position-item">
                    <span className="item">专业要求：</span>
                    {this.state.job_details.MajorNames}
                  </p>
                  <p className="position-item">
                    <span className="item">学校级别：</span>
                    {this.state.job_details.SchoolLevelNames}
                  </p>
                  <p className="position-item">
                    <span className="item">学校城市：</span>
                    {this.state.job_details.SchoolCityNames}
                  </p>
                </div>
              </section>
              <section className="job-details-content">
                <p className="position-title content">工作内容</p>
                <section
                  className="position-content"
                  dangerouslySetInnerHTML={{
                    __html: this.state.job_details.PosDescription
                  }}
                />
              </section>
              <section className="remark-area">
                <ul className="remark-list" />
              </section>
            </section>
          </div>

          <Toast icon="loading" show={this.state.showLoading}>
            加载中...
          </Toast>
          <Dialog
            type="ios"
            title={this.state.iosStyle.title}
            buttons={this.state.iosStyle.buttons}
            show={this.state.showIOS}
          >
            禁止
          </Dialog>
          {this.state.ifFire ? (
            <section className="remark-edit" onClick={e => e.stopPropagation()}>
              <div className="remark-edit-fire">
                <div
                  className="contents edit-remark"
                  data-text="写评论"
                  contentEditable="true"
                  ref={input => input && input.focus()}
                  onInput={this.handleFireState}
                />
              </div>
              <div
                className={
                  this.state.ifRemark
                    ? "remark-fire remark-active"
                    : "remark-fire"
                }
                onClick={this.state.ifRemark ? this.handleFire : null}
              >
                发布
              </div>
            </section>
          ) : (
            <section className="job-fire">
              <div className="remark" onClick={e => this.handleRemark(e, true)}>
                <span className="edit" />
                <span>点评</span>
              </div>
              <div className="fire" onClick={this.handleApplyJob}>
                申请职位
              </div>
            </section>
          )}
        </section>
        {/* 公司主页 */}
        {this.state.showCompany && (
          <Page ptr={false} infiniteLoader={false} style={{ zIndex: "519" }}>
            <CompanyModule
              goBack={this.handleCompanyHomepage}
              companyId={this.state.companyId}
            />
          </Page>
        )}
      </div>
    );
  }
}

export default JobDetailsModule;



