import React from "react";
import "./job-company.css";

const JobCompany = props => {
  return (
    <section className="etp-info">
      <img src={props.etpLogo} width="40" height="40" alt="logo"/>
      <section className="info">
        <p className="etpName ellipsis">
          <span className="title">{props.etpName}</span>
          {props.etpStatus === 4 && (<i className="vip"></i>)}
        </p>
        <p className="etpArea ellipsis">{props.etpArea}</p>
      </section>
    </section>
  );
};

export default JobCompany;



