import React from "react";
import ReactDOM from "react-dom";
import axios from "axios";
import {
  Page,
  Button,
  TextArea,
  Toast,
  Dialog,
  Tab,
  TabBody,
  NavBar,
  NavBarItem,
  InfiniteLoader
} from "react-weui";
import GlobalHeader from "../header/header";
import JobSub from "../job_module/job-sub";
import "../job_module/job-sub.css";
import "./companyModule.css";

class CompanyModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showLoading: false,
      company_details: {},
      job_list: [],
      job_list_page: 1,
      // 提示框
      iosStyle: {
        buttons: [
          {
            type: "default",
            label: "Cancel",
            onClick: null
          },
          {
            type: "primary",
            label: "OK",
            onClick: null
          }
        ]
      },
      showIOS: false,
      // 公司主页导航栏
      tab: 0
    };
    // 记录全局职位总数
    this.rowCount;
    // 处理获取在招职位内容
    this.handleCompanyJobs = this.handleCompanyJobs.bind(this);
    // 处理获取对话HR内容
    this.handleQa = this.handleQa.bind(this);
  }

  componentWillMount() {
    this.showLoading();
  }

  componentDidMount() {
    let url = "//api.zheyibu.com/company/app/" + this.props.companyId;
    axios
      .get(url)
      .then(res => {
        const company_details = res.data;
        this.setState({
          company_details: company_details,
          showLoading: false
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  handleCompanyJobs(e) {
    // =====================================================================//
    // 可优化！！！减少服务器请求次数
    this.setState(
      (prevState, props) => ({
        tab: 1,
        showLoading: true,
        job_list_page: 1
      }),
      function() {
        // 获取在招职位列表
        let url =
          "//api.zheyibu.com/company/CompanyJobs?companyId=" +
          this.props.companyId +
          "&page=" +
          this.state.job_list_page +
          "&pageSize=20";
        axios
          .get(url)
          .then(res => {
            const job_list = res.data.Positions.Results;
            this.rowCount = res.data.Positions.RowCount;
            this.setState((prevState, props) => ({
              job_list: job_list,
              job_list_page: prevState.job_list_page + 1,
              showLoading: false
            }));
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    );
  }
  handleQa(e) {
    this.setState({ tab: 2 });
  }

  showLoading() {
    this.setState({ showLoading: true });
  }
  render() {
    let _this = this;
    return (
      <section className="company-details-module">
        <div className="just-overlay">
          <GlobalHeader headerName="公司信息" goBack={this.props.goBack} />
          <section className="company-top-intro">
            <img
              src={this.state.company_details.LogoUrl}
              width="60"
              height="60"
            />
            <div className="intro-title">
              <p className="title-name">{this.state.company_details.Name}</p>
              <p className="title-attention">
                已有{this.state.company_details.FollowStatistic}人关注
              </p>
            </div>
            {this.state.company_details.IsFollowed ? (
              <div className="btn attentioned">已关注</div>
            ) : (
              <div className="btn attention">关注</div>
            )}
          </section>
          <section className="company-details">
            <div className="tab-list">
              <Tab>
                <NavBar>
                  <NavBarItem
                    active={this.state.tab == 0}
                    onClick={e => this.setState({ tab: 0 })}
                  >
                    <span className="nav-title">公司详情</span>
                  </NavBarItem>
                  <NavBarItem
                    active={this.state.tab == 1}
                    onClick={e => this.handleCompanyJobs(e)}
                  >
                    <span className="nav-title">招聘职位</span>
                  </NavBarItem>
                  <NavBarItem
                    active={this.state.tab == 2}
                    onClick={e => this.handleQa(e)}
                  >
                    <span className="nav-title">最新评论</span>
                  </NavBarItem>
                </NavBar>
                <TabBody>
                  <section
                    style={{ display: this.state.tab == 0 ? null : "none" }}
                  >
                    <div className="details-summary">
                      <div className="summary-intro">
                        <p className="intro intro-industry">
                          <label>行业：</label>
                          <span>{this.state.company_details.Industry}</span>
                        </p>
                        <p className="intro intro-nature">
                          <label>性质：</label>
                          <span>{this.state.company_details.EtpType}</span>
                        </p>
                        <p className="intro intro-scale">
                          <label>规模：</label>
                          <span>{this.state.company_details.Scale}</span>
                        </p>
                      </div>
                      <div className="content-sum">
                        <p className="sum-title">公司介绍</p>
                        <div
                          className="sum-details"
                          dangerouslySetInnerHTML={{
                            __html: this.state.company_details.Introduction
                          }}
                        />
                      </div>
                    </div>
                  </section>
                  <section
                    style={{ display: this.state.tab == 1 ? null : "none" }}
                  >
                    <InfiniteLoader
                      onLoadMore={(resolve, finish) => {
                        if (_this.state.job_list.length === _this.rowCount) {
                          finish();
                        } else {
                          let url =
                            "//api.zheyibu.com/company/CompanyJobs?companyId=" +
                            _this.state.company_details.CompanyId +
                            "&page=" +
                            _this.state.job_list_page +
                            "&pageSize=20";
                          axios
                            .get(url)
                            .then(res => {
                              const job_list = res.data.Positions.Results;
                              _this.setState(
                                (prevState, props) => ({
                                  job_list: prevState.job_list.concat(job_list),
                                  job_list_page: prevState.job_list_page + 1,
                                  showLoading: false
                                }),
                                () => resolve()
                              );
                            })
                            .catch(function(error) {
                              console.log(error);
                            });
                        }
                      }}
                    >
                      <div className="joblist">
                        {this.state.job_list.map((key, index) => {
                          let props = {
                            PositionId: key.PositionId,
                            SalaryName: key.SalaryRange,
                            Position: key.PositionName,
                            CityName: key.CityName
                          };
                          return <JobSub position={props} key={index} />;
                        })}
                      </div>
                    </InfiniteLoader>
                  </section>
                  <section
                    style={{ display: this.state.tab == 2 ? null : "none" }}
                  >
                    <div className="hr">
                      <p className="hr-intro">
                        您好，我是上海领栋企业管理风控有限公司的HR，很高兴认识你，若有问题可向我提问，我会第一时间给予回复。
                      </p>
                      <TextArea
                        placeholder="请写下您的问题"
                        rows="8"
                        maxlength="200"
                      />
                      <Button>提交</Button>
                    </div>
                  </section>
                </TabBody>
              </Tab>
            </div>
          </section>
        </div>
        <Toast icon="loading" show={this.state.showLoading}>
          加载中...
        </Toast>
        <Dialog
          type="ios"
          title={this.state.iosStyle.title}
          buttons={this.state.iosStyle.buttons}
          show={this.state.showIOS}
        >
          弹框
        </Dialog>
      </section>
    );
  }
}

export default CompanyModule;



