import React from "react";
import axios from "axios";
import { Page, Button, Toast } from "react-weui";
import GlobalHeader from "../header/header";
import "./article_details.css";

class ArticleDetailsModule extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      article_details: "",
      showLoading: false
    };
  }

  componentWillMount() {
    this.showLoading();
  }

  componentDidMount() {
    let url = "//api.zheyibu.com/api/article/" + this.props.details.ArticleId;
    axios
      .get(url)
      .then(res => {
        const article_details = res.data;
        this.setState({
          article_details: article_details,
          showLoading: false
        });
      })
      .catch(function(error) {
        console.log(error);
      });
  }

  showLoading() {
    this.setState({ showLoading: true });
  }
  render() {
    return (
      <section className="article-details-module">
        <div className="just-overlay">
          <GlobalHeader headerName="求职攻略" goBack={this.props.goBack} />
          <section className="article-details">
            <p className="article-details-title">
              {this.state.article_details.ArticleTitle}
            </p>
            <section
              className="article-details-content"
              dangerouslySetInnerHTML={{
                __html: this.state.article_details.ArticleContent
              }}
            />
            <p className="article-time">
              {this.state.article_details.CreateDateName}
            </p>
          </section>
          <Toast icon="loading" show={this.state.showLoading}>
            加载中...
          </Toast>
        </div>
      </section>
    );
  }
}

export default ArticleDetailsModule;



