import React from "react";
import "./article.css";

const ArticleModule = props => (
  <section
    className="article-module"
    onClick={e => props.articleEvent(props.data.Article)}
  >
    <p className="article-title">
      <span className="title">求职攻略</span>
      <i
        className="article-close"
        onClick={e => {
          e.stopPropagation();
          props.closeArticle();
        }}
      />
    </p>
    <img src={props.data.Article.Thumb} width="100%" height="165" />
    <section className="article-info">
      <div className="info-name">
        <span className="time">{props.data.Article.CreateDate}</span>
        <p className="title ellipsis">{props.data.Article.ArticleTitle}</p>
      </div>
    </section>
  </section>
);

export default ArticleModule;



